:root{
    --colorText:#333;
    --fontHeding: 2rem;
    --txtUppercase: uppercase;
}


.navbar{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: auto;
    background: transparent;
}


.hero{
    width: 100%;
    height: 480px;
}
  
  
  .bgImage{
    width: 100%;
    height: 100%;
    background-image: url('./images/hero.png');
    background-repeat: repeat;
    background-position: center;
    background-size: cover;
    position: relative;
}

.bgImage::before{
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
}


.navbar-nav .nav-link {
    color: #fff;
    padding-right: 1rem!important;
    padding-left: 1rem!important;
}



.navbar-brand {
   font-size: 2.25rem!important;
   color: #2C7DB3; 
   font-weight: 500;   
}
.navbar-brand:hover{
    color: #2C7DB3; 
}
.navbar-brand span{
    color: rgb(14 173 0);
 }

 .navbar .active__class{    
    color: rgb(14 173 0);
    font-weight: 700;
}




 .caption{
    width: 50%;
    height: auto;
    padding: 20px;
    position: absolute;
    bottom: 45px;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    color: #FFF;
     
 }
 .caption h1{
    color: #fff;
    font-weight: 400;
    text-transform: uppercase;
    font-size: 2rem;
    margin-bottom: 1rem;
    line-height: 45px;
}
.caption p{
    font-size: 0.813rem;
    line-height: 24px;
}



.card-header{
    background-color: #2C7DB3!important; 
    color: #fff;
    padding: 1.1rem 0rem !important;
    
}

.card-header h5{
   margin: 0;
   font-size: 1.1rem;
}

.maindiv{
 position: relative;
}

.maindiv h2::after{
    width: 95px;
    height: 3px;
    background-color: rgb(0, 136, 79);
    margin: 0 auto;
    text-align: center;
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 110px;

}

.maindiv h2{
    color: #333;
}

.card-img, .card-img-top {
     border-top-left-radius: calc(0rem - 0px)!important;
     border-top-right-radius: calc(0rem - 0px)!important;
}



.article_section{
    background-color: #F5F7F8;
    position: relative;
}
.article_section h3{
   font-size: 2rem;
   color: #333;
}

.iconbg{
    background-color: #2C7DB3;
    width: 100px;
    height: 100px;
    margin: 0 auto;
}

.iconbg .watch{
    font-size: 2.5rem;
    color: #fff;
  
}

.article_section h3::after{
    width: 95px;
    height: 3px;
    background-color: rgb(0, 136, 79);
    margin: 0 auto;
    text-align: center;
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 110px;
}

.article_section h5{
    font-size: 1rem;
    color: #333;
    text-transform: uppercase;
    font-weight: 700;
 }

 .article_section::before{
    position: absolute;
    top: -34px;
    content: "";
    margin: auto;
    left: 0;
    right: 0;
    width: 0;
    height: 0;
    border-left: 22px solid transparent;
    border-right: 22px solid transparent;
    border-bottom: 34px solid #F5F7F8;
}


.cdbox{
    margin: 4rem 0 2rem 0;
}


.aside_section{
    position: relative;
}

.aside_section h4{
  font-size: var(--fontHeding);
  color: var(--colorText);
  text-transform: var(--txtUppercase);
}

.aside_section  h4::after{
    width: 95px;
    height: 3px;
    background-color: rgb(0, 136, 79);
    margin: 0 auto;
    text-align: center;
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 110px;
}

.aside_section .expbox{
    width: 100%;
    height: auto;
    padding: 25px;
    background-color: #F5F7F8;
    border-radius: 10px;
    position: relative;
}
.aside_section .expbox p{
       color: #6f6f6f;
}

.expbox::after{
    position: absolute;
    content: "";
    left: 41px;
    right: 0;
    width: 0;
    height: 0;
    bottom: -19px;
    border-left: 11px solid transparent;
    border-right: 11px solid transparent;
    border-top: 19px solid #f5f7f8;
}

.aside_section .title{
    display: flex;
    justify-content: center;
    align-items: self-start;
    flex-direction: column;
    margin-left: 15px;
}

.aside_section .title span{
    color: #6f6f6f;
}


.realkeyapp_section{
    background-color: #2C7DB3;
    width: 100%;
    height: auto;
    clear: both;
    display: table;
}


.content{
    width: 50%;
    min-height: 415px;
    height: 100%;
    float: left;
    background-color: #2C7DB3;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imgdiv{
    width: 50%;
    height: auto;
    float: left;
  }

  .realkeyapp_section h2{
    font-size: var(--fontHeding);
    color: #fff;
    text-transform: var(--txtUppercase);

  }

  .realkeyapp_section .content p{
     color: #fff;
     font-size: 0.813rem;
  }
  .content img{
    width: 9rem;
  }

  .app{
    width: 55px;
    height: 55px;
    border-radius: 15px;
    padding: 10px 12px;
    background-image: linear-gradient(to bottom,#1abff7, #258ed3);
    margin-right: 21px;
  }

  .app .apple{
      font-size: 2rem;
      color: #fff;
  }

  .footer{
      background-color: #1e2023;
  }
  
  .footer .circle{
    width: 42px;
    height: auto;
    padding: 9px 5px;
    background-color: #3f4042;
    border-radius: 50%;
    text-align: center;
    display: inline-block;
    margin: 5px;
  } 
  .footer p{
      color: #6c6c6d;
      margin-left: 30px;
      font-size: 0.813rem;
      margin-bottom: 0;
      padding-top: 4px;
  }

  .footer .fmenu ul{
    margin-bottom: 0!important;
}

.fmenu{
    display: flex;
    justify-content: center;
    align-items: center;
}
  .fmenu li{
   display: inline-block;
  }
  .fmenu li a{
    color: #6c6c6d;
    font-size: 0.813rem;
    margin-left: 10px;
   }






/* X-Small devices (portrait phones, less than 576px)
No media query for `xs` since this is the default in Bootstrap*/

/* Small devices (landscape phones, 576px and up)*/

@media (min-width: 320px) and (max-width: 480px) { 
    .caption h1{
       display: none;
    }
    .caption p{
        display: none;
     }


    .caption {
        width: 85%;
        bottom: 6px;
       
    }
    .signupkey h6{
        line-height: 24px;
        font-size: 13px;
    }

    .navbar-nav .nav-link {
       border-bottom: 1px solid #292929;
    }
    
    .navbar-toggler-icon {
        width: 1.5em;
        height: 1.5em;
        vertical-align: middle;
        background-image: url('./images/menu.png');
        background-repeat: repeat;
        background-position: center;
    
    }
    
    .content{
        width: 100%;
    }
    .imgdiv{
        width: 100%;
      }
   .aside_section h4::after {
         top: 144px;
    }
    
    .article_section h3 {
         margin-bottom: 22px;
    }

.cdbox{
    margin: 3rem 0; 
  }

  .navbar-nav {
    display: flex;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    background-color: black;
    z-index: 9999;
    padding: 1rem;
}


 }


/* Medium devices (tablets, 768px and up)*/
@media (min-width: 480px) and (max-width: 768px) { 
    .caption h1{
        display: none;
     }
     .caption p{
         display: none;
      }
 
      .navbar-nav .nav-link {
        border-bottom: 1px solid #292929;
     }
 
     .caption {
         width: 85%;
         bottom: 6px;
        
     }
     .signupkey h6{
         line-height: 24px;
         font-size: 13px;
     }
     
     .navbar-toggler-icon {
         width: 1.5em;
         height: 1.5em;
         vertical-align: middle;
         background-image: url('./images/menu.png');
         background-repeat: repeat;
         background-position: center;
     
     }
     
     .content{
         width: 100%;
     }
     .imgdiv{
         width: 100%;
       }
    .aside_section h4::after {
          top: 144px;
     }
     
     .article_section h3 {
          margin-bottom: 22px;
     }
 
 .cdbox{
     margin: 3rem 0; 
   }
 
   .navbar-nav {
     display: flex;
     flex-direction: column;
     padding-left: 0;
     margin-bottom: 0;
     list-style: none;
     background-color: black;
     z-index: 9999;
     padding: 1rem;
 }

 }

